<template>
    <q-page>
      <div v-if="!isBackendAvailable">
        <backend-status></backend-status>
      </div>
    
      <div v-if="isBackendAvailable" class="q-pa-md">
  
        <q-scroll-area style="height: calc(100vh - 180px);">
          <div v-if="recordings.length > 0" class="q-mt-md">
            <q-list>
              <q-item v-for="recording in reversedRecordings" :key="recording.id">
                <q-item-section side>
                  <q-btn
                    :icon="currentPlayingId === recording.id ? 'pause' : 'play_arrow'"
                    @click="currentPlayingId === recording.id ? stopPlayback() : playRecording(recording.id)"
                  />
                </q-item-section>
                <q-item-section>
                  <q-item-label>{{ recording.name }}</q-item-label>
                  <q-item-label caption>{{ formatDate(recording.date) }} - {{ formatDuration(recording.duration) }}</q-item-label>
                </q-item-section>
                <q-item-section side>
                  <q-btn icon="delete" color="red" @click="deleteRecording(recording.id)" />
                </q-item-section>
              </q-item>
            </q-list>
          </div>
        </q-scroll-area>
  
  
        <q-card-section class="fixed-bottom q-pb-lg">
            <div v-if="isRecording" class="q-pa-none">
            <div class="q-mb-md text-center">
                <div>{{ currentRecordingName }}</div>
                <div>{{ displayDuration }}</div>
            </div>
            </div>
          <div class="row justify-center items-center">
            <q-btn size='xl' round color="primary" :icon="isRecording ? 'stop' : 'mic'" @click="toggleRecording" />
          </div>
        </q-card-section>
  
      </div>
    </q-page>
  </template>
  
  <script>
import { useInstrumentStore } from '@/stores/instruments-store';
import { api_startRecording, api_stopRecording, api_playRecording, api_stopPlayback, api_deleteRecording, api_getRecordings, api_getRecorderStatus } from '@/services/apiServices';
import BackendStatus from '@/components/BackendStatus.vue';
import { computed } from 'vue'
  
  export default {
    components: {
      BackendStatus,
    },
    setup() {
      const store = useInstrumentStore();
      const isBackendAvailable = computed(() => store.isBackendAvailable);

      return {
        isBackendAvailable,
      }
    },
    data() {
      return {
        isRecording: false,
        currentRecordingName: '',
        currentRecordingStartTime: null,
        currentPlayingId: null,
        recordings: [],
        recordingDuration: 0,
        recordingDurationInterval: null,
      };
    },
    computed: {
      reversedRecordings() {
        return [...this.recordings].reverse();
      },
      displayDuration() {
        const minutes = Math.floor(this.recordingDuration / 60);
        const secs = Math.floor(this.recordingDuration % 60);
        const millis = Math.floor((this.recordingDuration % 1) * 10); // Use 10 instead of 1000 for one decimal place

        const formattedMinutes = String(minutes).padStart(2, '0');
        const formattedSeconds = String(secs).padStart(2, '0');

        return `${formattedMinutes}:${formattedSeconds},${millis}`;
      }
    },
    methods: {
      toggleRecording() {
        if (this.isRecording) {
          this.stopRecording();
        } else {
          this.startRecording();
        }
      },
      calculateDuration() {
        const now = Date.now() / 1000.0;
        this.recordingDuration = now - this.currentRecordingStartTime;
      },
      startDurationInterval() {
        this.recordingDurationInterval = setInterval(() => {
          this.calculateDuration();
        }, 500);
      },
      stopDurationInterval() {
        if (this.recordingDurationInterval) {
          clearInterval(this.recordingDurationInterval);
          this.recordingDurationInterval = null;
          this.recordingDuration = 0;
        }
      },
      setCurrentRecordingName() {
        const defaultName = "Aufnahme";
        let highestNumber = 0;
        for (let i = 0; i < this.recordings.length; i++) {
          const recordingName = this.recordings[i].name;
          if (!recordingName.startsWith(defaultName)) {
            continue;
          }
          const nameParts = recordingName.split(' ');
          const numberPart = nameParts[1];
          const recordingNumber = parseInt(numberPart);
          if (!isNaN(recordingNumber) && recordingNumber > highestNumber) {
            highestNumber = recordingNumber;
          }
        }

        this.currentRecordingName = `Aufnahme ${highestNumber + 1}`;
      },
      startRecording() {
        this.setCurrentRecordingName();
        this.currentRecordingStartTime = Date.now() / 1000.0;

        if (this.currentPlayingId) {
          this.stopPlayback();
        } 

        api_startRecording(this.currentRecordingName, this.currentRecordingStartTime)
          .then(response => {
            this.isRecording = true;

            const store = useInstrumentStore();
            store.setRecorderActive(true);

            this.startDurationInterval();
          })
          .catch(error => {
            this.isRecording = false;
            console.error("Error starting recording:", error);
          });
      },
      stopRecording() {
        this.stopDurationInterval();

        api_stopRecording()
          .then(() => {
            this.isRecording = false;
            setTimeout(() => {
              this.fetchRecordings();
            }, 400);

            const store = useInstrumentStore();
            store.setRecorderActive(false);
          })
          .catch(error => {
            console.error("Error stopping recording:", error);
          });
      },
      playRecording(id) {
        if (this.isRecording) {
          this.stopRecording();
        }
        api_playRecording(id)
          .then(() => {
            this.currentPlayingId = id;
          })
          .catch(error => {
            console.error("Error playing recording:", error);
          });
      },
      stopPlayback() {
        api_stopPlayback()
          .then(() => {
            this.currentPlayingId = null;
          })
          .catch(error => {
            console.error("Error stopping playback:", error);
          });
      },
      deleteRecording(id) {
        api_deleteRecording(id)
          .then(() => {
            this.fetchRecordings();
          })
          .catch(error => {
            console.error("Error deleting recording:", error);
          });
      },
      fetchRecordings() {
        api_getRecordings()
          .then(response => {
            this.recordings = response.data;
          })
          .catch(error => {
            console.error("Error fetching recordings:", error);
          });
      },
      fetchStatus() {
        api_getRecorderStatus()
          .then(response => {
            this.isRecording = response.data.isRecording;
            this.currentPlayingId = response.data.currentPlayingId;
            if (this.isRecording) {
              this.currentRecordingName = response.data.currentRecordingName;
              this.currentRecordingStartTime = response.data.currentRecordingStartTime; 
              if (!this.recordingDurationInterval) {
                this.calculateDuration();
                this.startDurationInterval();
              }
            }
            else {
              const store = useInstrumentStore();
              store.setRecorderActive(false);
            }
          })
          .catch(error => {
            console.error("Error fetching status:", error);
          });
      },
      formatDate(date) {
        const recordingDate = new Date(date);
        const now = new Date();
        const diffMs = now - recordingDate;
        const diffMins = Math.floor(diffMs / 60000);
  
        if (diffMins <= 15) {
          return `vor ${diffMins} Minuten`;
        }
  
        return recordingDate.toLocaleTimeString();
      },
      formatDuration(seconds) {
        const minutes = Math.floor(seconds / 60);
        const secs = Math.floor(seconds % 60);
        return `${minutes}m ${secs}s`;
      }
    },
    mounted() {
      this.fetchRecordings();
      this.fetchStatus();
    },
    unmounted() {
      this.stopDurationInterval();
    }
  };
  </script>
  
  <style scoped lang="scss">
  .q-card-section {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .fixed-bottom {
    position: fixed;
    bottom: 0;
    width: 100%;
    background: $grey-10;
  }
  </style>